import './css/App.css';
import React from 'react';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import LocalCafeIcon from '@material-ui/icons/LocalCafe';
import RestaurantIcon from '@material-ui/icons/Restaurant';
import FastfoodIcon from '@material-ui/icons/Fastfood';
import CardList from './components/CardList'
import {Typography} from "@material-ui/core";
import data from "./components/data.json"
import Fab from '@material-ui/core/Fab';
import EditIcon from '@material-ui/icons/Edit';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles(theme => ({
    fab: {
        margin: 0,
        top: 'auto',
        left: 'auto',
        bottom: 20,
        right: 20,
        position: 'fixed',
    },
}));

function App() {
    const classes = useStyles();
    const types = ["Caffe", "Bakery", "Restaurant"]
    const [value, setValue] = React.useState(1);
    const [dataType, setDataType] = React.useState("Bakery");

    const handleChange = (event, newValue) => {
        setValue(newValue);
        setDataType(types[newValue])
    };

    return (
      <div className="App">
        <div className="App-header">
            <h2>
              까리단길 맛집 리스트
            </h2>
        </div>
        <Tabs
          value={value}
          variant="fullWidth"
          onChange={handleChange}
          indicatorColor="primary"
          textColor="primary"
          aria-label="icon label tabs example"
        >
          <Tab label="Caffe" icon={<LocalCafeIcon />} />
          <Tab label="Bakery" icon={<FastfoodIcon />} />
          <Tab label="Restaurant" icon={<RestaurantIcon />} />
        </Tabs>
        <CardList data={data[dataType]} />
        <Fab color="secondary" aria-label="edit" href="http://naver.me/Fn22oIl7" className={classes.fab}>
          <EditIcon />
        </Fab>
        <div className="App-footer">
            <Typography>Ref. <b><a href="https://open.kakao.com/o/gyKFLXMb">은하작교</a></b> / Made by <b>까치2단지 개발쟁이</b></Typography>
        </div>
      </div>
    );
}

export default App;
