import CustomCard from "./CustomCard";
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        justify: "center"
    },
    gridContainer: {
        alignContent: "center",
        alignItems: "center",
        marginBottom: "auto",
        marginTop: "auto"
    },
    gridItem: {
        minWidth: 300,
        minHeight: 350,
        alignContent: "center"
    }
}));

export default function CardList(props) {
    const classes = useStyles();
    return (
        <div className={classes.root}>
            <Grid container spacing={3} justify="center" className={classes.gridContainer}>
                { props.data.map( (card, index) => {
                    return <Grid item className={classes.gridItem} key={index}>
                        <CustomCard title={card.title} description={card.description} imageSrc={card.imageSrc} infoUrl={card.infoUrl} recomMenu={card.recomMenu}/>
                    </Grid>
                } )}
            </Grid>
        </div>
    );
}