import '../css/Card.css';
import React, {useEffect} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import CardActions from '@material-ui/core/CardActions';
import IconButton from '@material-ui/core/IconButton';
import { ReactComponent as NaverSvg } from './naver.svg';
import {ReactComponent as KakaoSvg} from "./kakao.svg";

const useStyles = makeStyles((theme) => ({
    root: {
        maxWidth: 300,
        minWidth: 300,
    },
    media: {
        height: 0,
        paddingTop: '56.25%', // 16:9
    },
    expand: {
        transform: 'rotate(0deg)',
        marginLeft: 'auto',
        transition: theme.transitions.create('transform', {
            duration: theme.transitions.duration.shortest,
        }),
    },
    expandOpen: {
        transform: 'rotate(180deg)',
    },
    imageIcon: {
        Display: 'flex',
        height: '32px',
        width: '32px',
        justifyContent: 'center',
        alignItems: 'center'
    }
}));

export default function CustomCard(props) {
    const classes = useStyles();
    useEffect(() => {
        createKakaoButton()
    }, [])

    const createKakaoButton = () => {
        if (window.Kakao) {
            const kakao = window.Kakao
            // 중복 initialization 방지
            if (!kakao.isInitialized()) {
                // 두번째 step 에서 가져온 javascript key 를 이용하여 initialize
                kakao.init(process.env.REACT_APP_KAKAO_KEY)
            }
        }
    }

    const sendKakao = () => {
        const kakao = window.Kakao
        const sharedUrl = props.infoUrl
        console.log(sharedUrl)
        kakao.Link.sendDefault({
            // Render 부분 id=kakao-link-btn 을 찾아 그부분에 렌더링을 합니다
            // container: '#kakao-link-btn',
            objectType: 'feed',
            content: {
                title: '까리단길 맛집 공유',
                description: props.title,
                imageUrl: 'https://kkachit.github.io/kkaridan-ave/kkachi.jpg',
                link: {
                    mobileWebUrl: sharedUrl,
                    webUrl: sharedUrl,
                },
            },
            buttons: [
                {
                    title: '네이버 정보 보기',
                    link: {
                        mobileWebUrl: sharedUrl,
                        webUrl: sharedUrl,
                    },
                }
            ],
        })
    }

    return (
        <div>
            <Card className={classes.root}>
                <CardHeader title={props.title} />
                <CardMedia
                    className={classes.media}
                    image={props.imageSrc}
                />
                <CardContent>
                    <Typography paragraph variant="body2" component="p">
                        {props.description}
                    </Typography>
                    <Typography>
                        {props.recomMenu}
                    </Typography>
                </CardContent>
                <CardActions disableSpacing>
                    <IconButton>
                        <a href={props.infoUrl}>
                            <NaverSvg className={classes.imageIcon} />
                        </a>
                    </IconButton>
                    <IconButton id="kakao-link-btn" onClick={sendKakao}>
                        <div>
                        <KakaoSvg className={classes.imageIcon} />
                        </div>
                    </IconButton>
                </CardActions>
            </Card>
        </div>
    );
}